// Calendar used inside a card

.card-calendar {
  .card-header {
    border-bottom: 0;
  }

  table {
    background: transparent;
    tr {
      & > td {
        &:first-child {
          border-left-width: 0;
        }
        &:last-child {
          border-right-width: 0;
        }
      }
    }
  }
}

// Widget

.widget-calendar {
  position: relative;
  z-index: 0;

  td,
  th {
    border-color: transparent;
    text-align: center;
  }

  .fc-toolbar {
    margin-top: 1.25rem;

    h2 {
      font-size: $font-size-base;
    }
  }

  .fc-daygrid-day-number {
    text-align: center;
    width: 100%;
    padding: 0;
  }

  .fc {
    table {
      font-size: $font-size-sm;
    }
    th {
      padding: 0.75rem 0.5rem;
      font-size: $font-size-xs;
    }
  }

  .fc-scrollgrid{
    border: none;
  }
}
